import React from "react";
import "./../App.css";
import "./../Cards.css";

export default class Logistic extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      raclettefx: "bgimg",
      raclettebool: false,
      ceremofx: "bgimg",
      ceremobool: false,
      soireefx: "bgimg",
      soireebool: false,
    };
    this.overRaclette = this.overRaclette.bind(this);
    this.overCeremo = this.overCeremo.bind(this);
    this.overSoiree = this.overSoiree.bind(this);
    this.showInfoRaclette = this.showInfoRaclette.bind(this);
    this.showInfoCere = this.showInfoCere.bind(this);
    this.showInfoSoiree = this.showInfoSoiree.bind(this);
  }
  overRaclette() {
    var val1 = ""
    var val2 = ""
    if (this.state.raclettebool === false) {
      val1 = "bgimg active";
      val2 = true;
    }
    else {
      val1 = "bgimg"
      val2 = false
    }
    this.setState({
      raclettefx: val1,
      raclettebool: val2,
    });
  }
  overCeremo() {
    var val1 = ""
    var val2 = ""
    if (this.state.ceremobool === false) {
      val1 = "bgimg active";
      val2 = true;
    }
    else {
      val1 = "bgimg"
      val2 = false
    }
    this.setState({
      ceremofx: val1,
      ceremobool: val2,
    });
  }
  overSoiree() {
    var val1 = ""
    var val2 = ""
    if (this.state.soireebool === false) {
      val1 = "bgimg active";
      val2 = true;
    }
    else {
      val1 = "bgimg"
      val2 = false
    }
    this.setState({
      soireefx: val1,
      soireebool: val2,
    });
  }
  showInfoRaclette() {
    console.log("raclette")
  }
  showInfoCere() {
    console.log("cérémonie")
  }
  showInfoSoiree() {
    console.log("diner")
  }
  render() {
    return (
      <div className="row logistic-row">
        <div className="logistic-table">
          <div className="row">
            <div className="col">
              AVION
            </div>
          </div>
          <div className="row">
            <div className="col">
              TRAIN
            </div>
          </div>
          <div className="row">
            <div className="col">
              TAXI
            </div>
          </div>
          <div className="row">
            <div className="col">
              HEBERGEMENT
            </div>
          </div>
          <div className="row">
            <div className="col">
              RESTAURANTS RECOMMANDES
            </div>
          </div>
          <div className="row">
            <div className="col">
              PLAN DE CRANS-MONTANA
            </div>
          </div>
          <div className="row">
            <div className="col">
              FORFAIT
            </div>
          </div>
        </div>
    </div>
);
  }
}