import React from "react";
import "./../App.css";
import "./../Cards.css";

export default class Cards extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      raclettefx: "bgimg",
      raclettebool: false,
      ceremofx: "bgimg",
      ceremobool: false,
      soireefx: "bgimg",
      soireebool: false,
      rowDisplay : "row normal-display",
      racletteShow: false,
      racletteStat: "raclette-content raclette-hide",
      racletteTitle : "bgbanner-text",
      racletteBtn: "",
      racletteBtnTitle: "AFFICHER",
      racletteBg: "",
    };
    this.overRaclette = this.overRaclette.bind(this);
    this.overCeremo = this.overCeremo.bind(this);
    this.overSoiree = this.overSoiree.bind(this);
    this.showInfoRaclette = this.showInfoRaclette.bind(this);
    this.showInfoCere = this.showInfoCere.bind(this);
    this.showInfoSoiree = this.showInfoSoiree.bind(this);
  }
  overRaclette() {
    var val1 = "";
    var val2 = "";
    // if it is hover
    if (this.state.raclettebool === false) {
      // create hover effect
      val1 = "bgimg active";
      val2 = true;
    }
    // if mouse leaves
    else {
      // remove fx
      val1 = "bgimg";
      val2 = false;
      // if raclette is show on then
      console.log(this.state.racletteShow);
    }
    this.setState({
      raclettefx: val1,
      raclettebool: val2,
    });
    if (val2 === true && this.state.racletteShow === true) {
      this.setState({
        racletteBg: ""
      });
    }
    else if (val2 === false && this.state.racletteShow === false) {
      this.setState({
        racletteBg: ""
      });
    }
    else if ((val2 === false && this.state.racletteShow === true)){
      this.setState({
        racletteBg: "active"
      });
    }
  }
  overCeremo() {
    var val1 = ""
    var val2 = ""
    if (this.state.ceremobool === false) {
      val1 = "bgimg active";
      val2 = true;
    }
    else {
      val1 = "bgimg"
      val2 = false
    }
    this.setState({
      ceremofx: val1,
      ceremobool: val2,
    });
  }
  overSoiree() {
    var val1 = ""
    var val2 = ""
    if (this.state.soireebool === false) {
      val1 = "bgimg active";
      val2 = true;
    }
    else {
      val1 = "bgimg"
      val2 = false
    }
    this.setState({
      soireefx: val1,
      soireebool: val2,
    });
  }
  showInfoRaclette() {
    if (this.state.racletteShow === false) {
      this.setState({
        racletteShow: true,
        racletteTitle: "bgbanner-text rmoveup",
        racletteBtn: "rmovedown",
        racletteBtnTitle: "FERMER",
      })
      setTimeout(() => { this.setState({
        racletteStat: "raclette-content raclette-show",
      }) }, 500);
    }
    else {
      this.setState({
        racletteShow: false,
        racletteStat: "raclette-content raclette-hide",
        racletteTitle: "bgbanner-text",
        racletteBtn: "",
        racletteBtnTitle: "AFFICHER",
      })
    }
    

  }
  showInfoCere() {
    console.log("cérémonie")
  }
  showInfoSoiree() {
    console.log("diner")
  }
  render() {
    return (
      <div className="row">
        <div className="col-sm wedding-card mr-4 mb-4">
          <div class="centerBox" onMouseOver={this.overRaclette} onClick={this.showInfoRaclette} onMouseOut={this.overRaclette}>
            <img className={`${this.state.raclettefx} ${this.state.racletteBg}`}
            src="https://d3rrv21q7fx9b0.cloudfront.net/m/65d8c4094edebcb9/LA02_768x400-LA02_raclette-de-Noel.jpg" 
            alt="https://d3rrv21q7fx9b0.cloudfront.net/m/65d8c4094edebcb9/LA02_768x400-LA02_raclette-de-Noel.jpg"></img>
            <div class="categoryWrapper">
            <div className={`${this.state.racletteStat}`}>
              <p className="date-title">VENDREDI 18 MARS 2022 A 18H00</p>
              <p className="location-text">A CHETZERON</p>
              <p className="normal-text">LE TRANSPORT SE FERA PAR TELECABINES AU DEPART DE CRANS-MONTANA PUIS TRANSFER EN CHENILLETTES A PARTIR DE CRY D'ER JUSQU'A CHETZERON</p>
              <p className="normal-text">ATTENTION : LES TELECABINES S'ARRETENT A 19H. AU DELA, IL NE SERA PLUS POSSIBLE D'ACCEDER A CHETZERON
                </p>
            </div>
              <h5 className={this.state.racletteTitle}>RACLETTE PARTY</h5>
              <button className={this.state.racletteBtn}>
                <span>
                  <span>
                    <span data-attr-span={this.state.racletteBtnTitle}>
                      {this.state.racletteBtnTitle}
                    </span>
                  </span>
                </span>
              </button>
            </div>
          </div>
        </div>
      <div className="col-sm wedding-card mr-4 mb-4">
        <div class="centerBox" onMouseOver={this.overCeremo} onClick={this.showInfoCere} onMouseOut={this.overCeremo}>
          <img className={this.state.ceremofx}
            src="https://cdn0.mariages.net/img_r_476904/4/0/9/6/r10_2x_3_476904.jpg" 
            alt="https://cdn0.mariages.net/img_r_476904/4/0/9/6/r10_2x_3_476904.jpg"></img>
            <div class="categoryWrapper">
              <h5 className="bgbanner-text">CEREMONIE RELIGIEUSE</h5>
              <button>
                <span>
                  <span>
                    <span data-attr-span="AFFICHER">
                      AFFICHER
                    </span>
                  </span>
                </span>
              </button>
            </div>
        </div>
      </div>
      <div className="col-sm wedding-card mr-4 mb-4">
      <div class="centerBox" onMouseOver={this.overSoiree} onClick={this.showInfoSoiree} onMouseOut={this.overSoiree}>
      <img className={this.state.soireefx}
          src="https://cdn.galaxy.tf/thumb/sizeW600/sizeH450/zc/uploads/2s/cms_image/001/594/869/1594869955_5f0fc8c334a6b-thumb.jpg" 
          alt="https://cdn.galaxy.tf/thumb/sizeW600/sizeH450/zc/uploads/2s/cms_image/001/594/869/1594869955_5f0fc8c334a6b-thumb.jpg"></img>
          <div class="categoryWrapper">
              <h5 className="bgbanner-text">DINER ET SOIREE</h5>
              <button>
                <span>
                  <span>
                    <span data-attr-span="AFFICHER">
                      AFFICHER
                    </span>
                  </span>
                </span>
              </button>  
          </div>
        </div>
      </div>
    </div>
);
  }
}